/* ==========================================================================
   Base styles: opinionated defaults
   ========================================================================== */

   html {
    color: #222;
    font-size: 1em;
    line-height: 1.4;
}

/*
 * Remove text-shadow in selection highlight:
 * https://twitter.com/miketaylr/status/12228805301
 *
 * Vendor-prefixed and regular ::selection selectors cannot be combined:
 * https://stackoverflow.com/a/16982510/7133471
 *
 * Customize the background color to match your design.
 */

::selection {
    background: #b3d4fc;
    text-shadow: none;
}

/*
 * A better looking default horizontal rule
 */

hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #ccc;
    margin: 1em 0;
    padding: 0;
}

/*
 * Remove the gap between audio, canvas, iframes,
 * images, videos and the bottom of their containers:
 * https://github.com/h5bp/html5-boilerplate/issues/440
 */

audio,
canvas,
iframe,
img,
svg,
video {
    vertical-align: middle;
}

/*
 * Remove default fieldset styles.
 */

fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

/*
 * Allow only vertical resizing of textareas.
 */

textarea {
    resize: vertical;
}

/* ==========================================================================
   Browser Upgrade Prompt
   ========================================================================== */

.browserupgrade {
    margin: 0.2em 0;
    background: #ccc;
    color: #000;
    padding: 0.2em 0;
}




 /* ==========================================================================
   Author's custom styles
   ========================================================================== */
$font-stack: 'bureau-grot', sans-serif;
$primary-color: greenyellow;
$secondary-color: blue;
$dark-color: #444444;
$light-color: white;
* {
  box-sizing: border-box;
}
::selection {
  background: $primary-color;
}
::-moz-selection {
  background: $primary-color;
}
html {font-size: 16px;}

body {
  margin: 0;
  font-family: $font-stack;
  font-weight: 400;
  line-height: 1.45;
  background-color: $light-color;
  color: $dark-color;
  width: 100vw;
  overflow-x: hidden;
}
.interior {
  background-color: $primary-color;
}

nav {

}
ul {
  text-decoration: none;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
li {
  padding: 0 25px 0 0;
  margin: 15px 0 0;
  text-transform: uppercase;
  letter-spacing: .05rem;
  font-weight: bold;
  float: left;
}
li a, .interior a {
  border-bottom: 3px solid $dark-color;
}
li a:hover {
  border-bottom: 3px solid $primary-color;
}
li a.active {
  color: $dark-color;
  border-bottom: 3px solid $secondary-color;
}
li a.active:hover, .interior li a:hover, .interior a:hover {
  color: $secondary-color;
  border-bottom: 3px solid $secondary-color;
}
#stripe {
  width: 100vw;
  border-bottom: 20px solid $primary-color;
  position: absolute;
  top: 0px;
  left: 0;
  z-index: 1;

}
main, .about {
  width: 92vw;
  margin: 50px auto;
}
section {
  display: grid;
  grid-template-columns: 100%;
  grid-gap: 5vw;
  margin: 50px 0;
}
.individual {
  width: 100%;
  margin: 50px 0;
}
.day {
  padding: 15px;
  border: 3px solid $dark-color;
  box-shadow: 6px 6px $primary-color ;
  position: relative;
  top: 0;
  left: 0;
  transition: all .5s ease;
  cursor: pointer;
}
.day:hover {
  position: relative;
  /* border: 3px solid GreenYellow; */
  background-color: $primary-color;
  top: 3px;
  left: 3px;
  box-shadow: 0 0 $primary-color ;
}
.day:nth-child(3n+3) {
  margin-right: 0;
}
.room {
  position: absolute;
  bottom: 15px;
  left: 15px;
}
.print {
  position: absolute;
  bottom: 15px;
  right: 15px;
  cursor: pointer;
}
.only-print {display: none;}
.typeform-widget {
  font-family: $font-stack;
}

footer {
  display: none;
}
a {
  transition: all .5s ease;
}
a, a:hover, a:active, a:visited {
  color: $dark-color;
  text-decoration: none;
}
p {
  margin-bottom: 1.25em;
}
.description {
  margin-bottom: 50px;
}
.section-title {
  grid-column: span 1;
  text-transform: uppercase;
  letter-spacing: .1rem;
  color: $light-color;
  margin: 0;
}
.section-title span {
  background-color: $dark-color;
  padding: 4px 6px;
}
h1, h2, h3, h4, h5 {
  margin: 0;
  font-weight: 400;
  line-height: 1.15;
  font-family: $font-stack;
}
h1 {
  margin-top: 0;
  font-size: 4.552em;
  text-transform: uppercase;
  letter-spacing: .1rem;
  font-weight: 700;
  -webkit-text-stroke: 3px $dark-color;
  color: $light-color;
}
.interior h1 {
  color: $primary-color;
}

h2 {
  font-size: 2.441em;
  text-transform: uppercase;
  letter-spacing: .1px;
  font-weight: 700;
  margin-top: .25rem;
}

h3 {font-size: 1.953em;}

h4 {font-size: 1.563em;}

h5 {
  font-size: 1.25em;
  text-transform: uppercase;
  letter-spacing: .1px;
}

small, .text-small {
  font-size: 0.8em;
  font-weight: 700;
}
.link-small {
  font-size: 0.8em;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: .1px;
  border-bottom: 3px solid $dark-color;
}











 /* ==========================================================================
   Helper classes
   ========================================================================== */

/*
 * Hide visually and from screen readers
 */

 .hidden {
  display: none !important;
}

/*
* Hide only visually, but have it available for screen readers:
* https://snook.ca/archives/html_and_css/hiding-content-for-accessibility
*
* 1. For long content, line feeds are not interpreted as spaces and small width
*    causes content to wrap 1 word per line:
*    https://medium.com/@jessebeach/beware-smushed-off-screen-accessible-text-5952a4c2cbfe
*/

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap; /* 1 */
}

/*
* Extends the .visuallyhidden class to allow the element
* to be focusable when navigated to via the keyboard:
* https://www.drupal.org/node/897638
*/

.visuallyhidden.focusable:active,
.visuallyhidden.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
  white-space: inherit;
}

/*
* Hide visually and from screen readers, but maintain layout
*/

.invisible {
  visibility: hidden;
}

/*
* Clearfix: contain floats
*
* For modern browsers
* 1. The space content is one way to avoid an Opera bug when the
*    `contenteditable` attribute is included anywhere else in the document.
*    Otherwise it causes space to appear at the top and bottom of elements
*    that receive the `clearfix` class.
* 2. The use of `table` rather than `block` is only necessary if using
*    `:before` to contain the top-margins of child elements.
*/

.clearfix:before,
.clearfix:after {
  content: " "; /* 1 */
  display: table; /* 2 */
}

.clearfix:after {
  clear: both;
}


/* ==========================================================================
   EXAMPLE Media Queries for Responsive Design.
   These examples override the primary ('mobile first') styles.
   Modify as content requires.
   ========================================================================== */
 @media only screen and (min-width: 520px) {
   .head-contain {
     width: 75%;
   }
   header {
     position: relative;
   }
   nav {
     position: absolute;
     bottom: 16px;
     right: 0;
     margin: initial;
     padding: initial;
   }
   ul {
     text-decoration: none;
     list-style-type: none;
     text-align: right;
   }
   li {
     padding: 0;
     margin: 15px 0 0;
     text-transform: uppercase;
     letter-spacing: .05rem;
     font-weight: bold;
     float: none;
   }
}
@media only screen and (min-width: 630px) {
  .head-contain {
    width: 100%;
  }
  h1 {
    font-size: 5.652em;
  }
}

@media only screen and (min-width: 768px) {
    /* Style adjustments for viewports that meet the condition */
    main, .about {
      width: 95vw;
    }
    .individual {
      width: 75%;
    }
    section {
      display: grid;
      grid-template-columns: 46.25vw 46.25vw;
      grid-gap: 2.5vw 2.5vw;
      margin: 50px 0;
    }
    .section-title {
      grid-column: span 2;
    }
    h1 {
      font-size: 6.052em;
    }
}
@media only screen and (min-width: 810px) {
  .head-contain {
    width: 700px;
  }
}
@media only screen and (min-width: 930px) {
  .head-contain {
    width: 100%;
  }
}
@media only screen and (min-width: 1024px) {
    /* Style adjustments for viewports that meet the condition */
    main, .about {
      width: 95vw;
    }
    .individual {
      width: 50%;
      max-width: 600px;
    }
    section {
      display: grid;
      grid-template-columns: 30vw 30vw 30vw;
      grid-gap: 2.5vw 2.5vw;
      margin: 50px 0;
    }
    .section-title {
      grid-column: span 3;
    }
    h1 {
      font-size: 6.052em;
    }
}
@media only screen and (min-width: 1400px) {
    /* Style adjustments for viewports that meet the condition */
    main {
      width: 80vw;
      position: relative;
      left: 0;
    }
    section {
      display: grid;
      grid-template-columns: 25vw 25vw 25vw;
      grid-gap: 2.5vw 2.5vw;
      margin: 50px 0;
    }
    .about {
      width: 500px;
      padding: 40px;
    }
    #stripe {
      width: 0;
      height: 100vh;
      border-bottom: none;
      border-right: 20px solid $primary-color;
      position: fixed;
      top: 0;
      left: 0;
    }
    .section-title {
      grid-column: span 3;
    }
}
@media only screen and (min-width: 1800px) {
    /* Style adjustments for viewports that meet the condition */
    main {
      width: 87.5vw;
    }
    section {
      display: grid;
      grid-template-columns: 20vw 20vw 20vw 20vw;
      grid-gap: 2.5vw 2.5vw;
      margin: 50px 0;
    }
    .section-title {
      grid-column: span 4;
    }
}
@media print,
       (-webkit-min-device-pixel-ratio: 1.25),
       (min-resolution: 1.25dppx),
       (min-resolution: 120dpi) {
    /* Style adjustments for high resolution devices */
}


/* ==========================================================================
   Print styles.
   Inlined to avoid the additional HTTP request:
   https://www.phpied.com/delay-loading-your-print-css/
   ========================================================================== */

   @media print {
    *,
    *:before,
    *:after {
        background: transparent !important;
        -webkit-box-shadow: none !important;
        box-shadow: none !important;
        text-shadow: none !important;
    }

    a,
    a:visited {
        text-decoration: none;
    }

    article a[href]:after {
        content: " (" attr(href) ")";
    }

    abbr[title]:after {
        content: " (" attr(title) ")";
    }

    /*
     * Don't show links that are fragment identifiers,
     * or use the `javascript:` pseudo protocol
     */

    a[href^="#"]:after,
    a[href^="javascript:"]:after {
        content: "";
    }

    pre {
        white-space: pre-wrap !important;
    }
    pre,
    blockquote {
        border: 1px solid #999;
        page-break-inside: avoid;
    }

    /*
     * Printing Tables:
     * http://css-discuss.incutio.com/wiki/Printing_Tables
     */

    thead {
        display: table-header-group;
    }

    tr,
    img {
        page-break-inside: avoid;
    }

    p,
    h2,
    h3 {
        orphans: 3;
        widows: 3;
    }

    h2,
    h3 {
        page-break-after: avoid;
    }
    nav {
      display: none;
    }
    article {
      border-top: 20px solid $dark-color;
      border-bottom: 20px solid $dark-color;
      margin: 50px 0;
      padding: 50px 50% 50px 0;
    }
    footer {
      display: block;
      width: 50%;
    }
    h1 {
      font-size: 100px;
      color: rgba(0,0,0,0) !important;
    }
    p:last-of-type{margin-bottom: 0;}
    .no-print {display: none;}
    .only-print {display: block;}
    .print-underline {border-bottom: 2px solid $dark-color;}
}
